import styled from "styled-components";
import {theme} from "../../../styles/Theme";
import {font} from "../../../styles/Common";



const Main = styled.section`
  min-height: 100vh;
  background-color: #a392b2;
  display: flex;
`

const PhotoWrapper = styled.div`
  position: relative;
  z-index: 0;

  &::before {
    content: "";
    width: 360px;
    height: 470px;
    // border: 5px solid ${theme.colors.accent};
    border: 5px solid;
    border-image: linear-gradient(to left top, ${theme.colors.accent} 10%,  transparent 30%, transparent 70%,  ${theme.colors.accent} 90%);
    border-image-slice: 1;
    position: absolute;
    top: -24px;
    left: 24px;
    z-index: -1;

    @media ${theme.media.mobile} {
      width: 314px;
      height: 414px;
      top: -17px;
      left: 20px;
    }
  }


  @media ${theme.media.mobile} {
    margin-top: 65px ;
  }
`

const Photo = styled.img`
  width: 350px;
  height: 430px;
  object-fit: cover;
  object-position: top;
  margin-right: 20px;

  @media ${theme.media.mobile} {
    width: 310px;
    height: 380px;
  }

`
const MainTitle = styled.h1`
  ${font({weight: 400, Fmax: 27, Fmin: 20})};
  
  p {
  display: none;
  }
}
`

const Name = styled.h2`
  ${font({family: "'Josefin Sans', sans-serif", weight: 700, Fmax: 50, Fmin: 28})}
  letter-spacing: 0.05em;
  margin: 10px 0;

  span {
    position: relative;
    z-index: 0;
    white-space: nowrap;

    &::before {
      content: "";
      display: inline-block;
      width: 100%;
      height: 20px;
        // background-color: ${theme.colors.accent};
      background: linear-gradient(45deg, transparent, ${theme.colors.accent} 50%, transparent);
      position: absolute;
      bottom: 0;
      z-index: -1;
    }
  }

  @media ${theme.media.mobile} {
    margin: 12px 0;
  }
`

const SmallText = styled.span`
  font-size: 14px;
  font-weight: 400;

  @media ${theme.media.mobile} {
    margin-top: 18px;
    margin-bottom: 1px;
  }
`

export const S = {
    Main,
    PhotoWrapper,
    Photo,
    MainTitle,
    Name,
    SmallText,
}